//console.log("?");
                // eslint-disable-next-line no-unused-vars
                var m1 = 0;
                for (var i2 = x-0.2; i2 < x+ 0.25; i2 += 0.15)
                    for (var n2 = y-0.28; n2 < y + 0.02; n2 += 0.15) {

                        var pStart = new BMapGL.Point(i2, n2);
                        var pEnd = new BMapGL.Point(i2 + 0.15, n2 + 0.15);
                        var rectangle = new BMapGL.Polygon([
                            new BMapGL.Point(pStart.lng, pStart.lat),
                            new BMapGL.Point(pEnd.lng, pStart.lat),
                            new BMapGL.Point(pEnd.lng, pEnd.lat),
                            new BMapGL.Point(pStart.lng, pEnd.lat)
                        ], {
                            strokeColor: "white",
                            strokeWeight: 2,
                            strokeOpacity: 0.5,
                            fillColor: "pink"
                        });  //创建矩形
                        //添加覆盖物
                        m1++;
                        //console.log("!");
                        add_overlay();

                    }


            }
            //},

        },
        computed: {

        },
        watch: {
            //监听时间轴开始日期变化


        },
        mounted() {
            this.chinamap()
        }
    }
</script>
